/**
 * 🔌 API Service Layer
 *
 * This file handles all communication with our FastAPI backend.
 * It's designed to be beginner-friendly with clear type definitions!
 */

// Type definitions for our API responses
export interface Movie {
  movie_id: number;
  title: string;
  genres: string;
  average_rating?: number;
  total_ratings?: number;
}

export interface Recommendation {
  movie_id: number;
  title: string;
  genres: string;
  score: number;
  recommendation_type: string;
  explanation?: string;
}

export interface RecommendationResponse {
  user_id: number;
  recommendations: Recommendation[];
  total_count: number;
}

export interface UserProfile {
  user_id: number;
  total_ratings: number;
  average_rating: number;
  top_genres: Record<string, { mean: number; count: number }>;
  rating_distribution: Record<string, number>;
}

export interface ApiHealthCheck {
  status: string;
  model_loaded: boolean;
  movies_available: boolean;
}

export interface MovieRating {
  user_id: number;
  movie_id: number;
  rating: number;
}

// API Configuration
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";

/**
 * Generic API fetch wrapper with error handling
 */
async function apiRequest<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const url = `${API_BASE_URL}${endpoint}`;

  const config: RequestInit = {
    headers: {
      "Content-Type": "application/json",
      ...options.headers,
    },
    ...options,
  };

  try {
    const response = await fetch(url, config);

    if (!response.ok) {
      throw new Error(`API Error: ${response.status} ${response.statusText}`);
    }

    return await response.json();
  } catch (error) {
    console.error(`API Request failed for ${endpoint}:`, error);
    throw error;
  }
}

/**
 * 🎬 Movie API Functions
 */
export const movieApi = {
  /**
   * Get a list of movies with optional filtering
   */
  async getMovies(params?: {
    limit?: number;
    genre?: string;
    search?: string;
  }): Promise<Movie[]> {
    const queryParams = new URLSearchParams();

    if (params?.limit) queryParams.append("limit", params.limit.toString());
    if (params?.genre) queryParams.append("genre", params.genre);
    if (params?.search) queryParams.append("search", params.search);

    const endpoint = `/movies${
      queryParams.toString() ? `?${queryParams.toString()}` : ""
    }`;
    return apiRequest<Movie[]>(endpoint);
  },

  /**
   * Get detailed information about a specific movie
   */
  async getMovie(movieId: number): Promise<
    Movie & {
      average_rating?: number;
      total_ratings?: number;
      rating_distribution?: Record<string, number>;
    }
  > {
    return apiRequest<
      Movie & {
        average_rating?: number;
        total_ratings?: number;
        rating_distribution?: Record<string, number>;
      }
    >(`/movies/${movieId}`);
  },

  /**
   * Submit a rating for a movie
   */
  async rateMovie(rating: MovieRating): Promise<{
    message: string;
    user_id: number;
    movie_id: number;
    rating: number;
  }> {
    return apiRequest<{
      message: string;
      user_id: number;
      movie_id: number;
      rating: number;
    }>("/rate", {
      method: "POST",
      body: JSON.stringify(rating),
    });
  },
};

/**
 * 🤖 Recommendation API Functions
 */
export const recommendationApi = {
  /**
   * Get personalized movie recommendations for a user
   */
  async getRecommendations(params: {
    user_id: number;
    num_recommendations?: number;
    recommendation_type?: "user" | "item" | "hybrid";
  }): Promise<RecommendationResponse> {
    const requestBody = {
      user_id: params.user_id,
      num_recommendations: params.num_recommendations || 10,
      recommendation_type: params.recommendation_type || "hybrid",
    };

    return apiRequest<RecommendationResponse>("/recommendations", {
      method: "POST",
      body: JSON.stringify(requestBody),
    });
  },

  /**
   * Get explanation for why a movie was recommended
   */
  async getExplanation(
    userId: number,
    movieId: number
  ): Promise<{
    user_id: number;
    movie_id: number;
    movie_title: string;
    explanation: string;
    technical_note: string;
  }> {
    return apiRequest<{
      user_id: number;
      movie_id: number;
      movie_title: string;
      explanation: string;
      technical_note: string;
    }>(`/explain/${userId}/${movieId}`);
  },
};

/**
 * 👤 User API Functions
 */
export const userApi = {
  /**
   * Get a user's profile and preferences
   */
  async getUserProfile(userId: number): Promise<UserProfile> {
    return apiRequest<UserProfile>(`/users/${userId}/profile`);
  },
};

/**
 * 🏥 Health Check API
 */
export const healthApi = {
  /**
   * Check if the API is healthy and models are loaded
   */
  async getHealth(): Promise<ApiHealthCheck> {
    return apiRequest<ApiHealthCheck>("/health");
  },

  /**
   * Get basic API information
   */
  async getApiInfo(): Promise<{
    message: string;
    status: string;
    model_loaded: boolean;
    total_movies: number;
    docs: string;
    endpoints: Record<string, string>;
  }> {
    return apiRequest<{
      message: string;
      status: string;
      model_loaded: boolean;
      total_movies: number;
      docs: string;
      endpoints: Record<string, string>;
    }>("/");
  },
};

/**
 * 🎭 Genre utilities
 */
export const genreUtils = {
  /**
   * Extract all unique genres from a list of movies
   */
  extractGenres(movies: Movie[]): string[] {
    const genreSet = new Set<string>();

    movies.forEach((movie) => {
      const genres = movie.genres.split("|");
      genres.forEach((genre) => {
        if (genre.trim()) {
          genreSet.add(genre.trim());
        }
      });
    });

    return Array.from(genreSet).sort();
  },

  /**
   * Format genres for display
   */
  formatGenres(genres: string): string[] {
    return genres
      .split("|")
      .map((g) => g.trim())
      .filter(Boolean);
  },
};

/**
 * 🔧 Utility functions
 */
export const utils = {
  /**
   * Format rating as stars
   */
  formatRatingAsStars(rating: number): string {
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 >= 0.5;

    let stars = "⭐".repeat(fullStars);
    if (hasHalfStar) stars += "✨";

    return stars;
  },

  /**
   * Format rating as number with one decimal place
   */
  formatRating(rating: number): string {
    return rating.toFixed(1);
  },

  /**
   * Extract year from movie title
   */
  extractYear(title: string): string | null {
    const yearMatch = title.match(/\((\d{4})\)/);
    return yearMatch ? yearMatch[1] : null;
  },

  /**
   * Clean movie title (remove year)
   */
  cleanTitle(title: string): string {
    return title.replace(/\s*\(\d{4}\)\s*$/, "").trim();
  },
};

export default {
  movieApi,
  recommendationApi,
  userApi,
  healthApi,
  genreUtils,
  utils,
};
